body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #303030;
  color: #FFFFFF;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
main{
  justify-content:center;
  display:flex;
  flex-direction: column;
  align-items:center;
  
}
body{
  background-image:url('./cacti-banner-b.png');
}
body,html{
  width:100%;
  height:100%;
}
#mint-info-box{
  width:352px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position:relative;
}
#price-card {
    width: fit-content;
    padding: 5px;
    height: 20px;
    background: green;
    position: absolute;
    top: 5px;
    justify-content: center;
    animation: flex;
    display:flex;
    left: 5px;
    align-items: center;
}
#mint-card{
    width: fit-content;
    padding: 5px;
    height: 20px;
    background: green;
    position: absolute;
    top: 5px;
    justify-content: center;
    animation: flex;
    display:flex;
    right: 5px;
    align-items: center;
}
#connect-btn{
  width:100%;
  height:55px;
  display:flex;
  justify-content:center;
  align-items:center;
}
#mint-btn{
  width:100%;
  height:55px;
  display:flex;
  justify-content:center;
  align-items:center;
  background:green;
  color:white;
}
#mint-container{
  width:100%;
  height:55px;
  display:flex;
  justify-content:center;
  align-items:center;
}
#minting-action{
  width:20px!important;
  height:20px!important;
}
#main-box{
  display:flex;
  width: 950px;
  padding:30px;
  background: linear-gradient(0deg, rgba(112,233,161,1) 0%, rgba(118,73,193,1) 100%);

}

#tabpanel-row{
  display:flex;
  margin:10px 0px;
}

@media(max-width:700px){
  #main-box{
    width:unset;
    padding:unset;
    flex-direction:column-reverse
  }
  #mint-info-box{
    width:unset;
  }
  #para-text{
    width: 85%;
  }
}
#text-card-box{
  width:100%;
  padding:30px;
}
#title-text{
  margin:0px;
}
#para-text{
  margin:0px;
}
#app-box{
  background: #2c2c2c;
  padding:20px;
  display:flex;
  flex-direction: column;
  align-items:center;
}
#button-grp-wrap{
  width:fit-content;
  margin:20px 0px;
}

#button-grp-wrap>div>button{
  border-radius:30px;
  width:100px;
  margin:0px 10px;
  display:flex;
  justify-content:center;
  align-items:center;
  border: none;
  outline: none;
  background:#0e782d;
  color:white;
  font-weight:900;
}
#button-grp-wrap>div>button:hover{
background:#19c04a;}
#button-grp-wrap>div{
  box-shadow:unset;
}

#drop-rate-box{
  width:fit-content;
  background: #2c2c2c;
  display:flex;
  justify-content:center;
  padding:20px;
}
#drop-rate-btns > div > button {
  width:200px;
  height:30px;
  border-radius:0px;
  background:#0342b3;
  color:white;
  display:flex;
  justify-content:center;
  align-items:center;
  font-weight:900;
  outline:none;
  border:2px solid #275bbb;
  margin:20px 20px;
}
#drop-rate-btns > div > button:focus {
  outline:none; 
}

#drop-rate-btns > div{
  box-shadow:unset;
}

#tabber-outer-wrap{
  width:100%;
  display:flex;
  justify-content:center;
  flex-direction:column;
  align-items:center;
  padding:20px 0px;
}

#drop-rate-title{
  margin:0px;
  padding-bottom:20px;
}
#list-image-box{
  width: 60px;
  height: 60px;
  overflow: hidden;
  background: #af78e1;
  border-radius: 5px;
  position:relative;
}
#list-image-box > img {
  width:100%;
  

}
#body-pixil{
  transform: translateY(-20px);
}

#list-row{
  display:flex;
}
@media(max-width:700px){
  #list-row{
    flex-direction: column;
    align-items:center;
  }
  #drop-rate-btns > div{
    flex-direction: unset;
  }
  #app-box{
    padding:0px;
  }
  #drop-rate-btns > div > button{
    width:unset;
  }
  #tabpanel-row{
    flex-direction: column!important;
    align-items:center;
  }
  #tabbs{
    width:100%!important;
    margin:5px 0px!important;
  }
}
#drop-rate-btns > div:nth-child(2) > ul {
  display:flex;
  justify-content:center;
  border-top:2px solid #0000003b;
  border-bottom:2px solid #0000003b;
}

#drop-rate-btns > div:nth-child(2) > ul > li {
  width: 50%;
  display: flex;
  justify-content: center;
}


#drop-rate-btns > div:nth-child(2) > ul > li > div:nth-child(2) {
  display:flex;
  justify-content:center;
  align-items:center;
  flex-direction: column;
}

#drop-rate-btns > div:nth-child(2) > ul > li > div:nth-child(2) > span {
  margin:0px 20px;
}

#list-row > li{
  border:2px solid #0000003b;
  margin:5px;
  border-radius:5px;
  width:300px;
}

#list-row > li > div:nth-child(2){
  margin:0px 10px;
}


#body-section{
 padding:10px;
}

#list-row{
  padding-top:0px;
  padding-bottom:0px;
  justify-content:center;
}

#list-row > li > div:nth-child(1){
  font-size:12px;
}

#tabb > div > button {
  background: #af78e1;
  margin:0px 20px;
  border-radius:5px;
  padding:10px 25px;
  font-weight:900;
}
#tabb > div{
  background:transparent;
}